import route from "color-convert/route"

export class Component {
  constructor(payload = {}) {
    const { tagName = 'div', 
    state = {}, 
    props = {}
  } = payload   //Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ
    this.el = document.createElement(tagName)   //Î©îÎ™®Î¶¨ÏÉÅÏóêÏÑúÎßå Ï†ÄÏû•
    this.state = state
    this.props = props
    this.render()
  }
  render() {    //ÌïòÏúÑ ÌÅ¥ÎûòÏä§ÏóêÏÑú Ïò§Î≤ÑÎùºÏù¥Îìú Îê† Í≤É

  }
}


//Router
export function createRouter(routes) {
  return function() {
    window.addEventListener('popstate', () => {
      routeRender(routes)
    })
    routeRender(routes)
  }
}